
table.data-grid {
  table-layout: fixed;
  border-collapse: collapse;
}

table.data-grid td.cell.updated,
table.data-grid th.cell.updated {
  background-color: rgba(0, 145, 253, 0.16);
  -webkit-transition : background-color 0ms ease ;
  -o-transition : background-color 0ms ease ;
  transition : background-color 0ms ease ;
}
table.data-grid td.cell,
table.data-grid th.cell {
  height: 17px;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  cursor: cell;
  background-color: unset;
  -webkit-transition : background-color 500ms ease;
  -o-transition : background-color 500ms ease;
  transition : background-color 500ms ease;
  vertical-align: middle;
  text-align: right;
  border: 1px solid #DDD;
  padding: 0;
}
table.data-grid td.cell.selected,
table.data-grid th.cell.selected {
  border: 1px double rgb(33, 133, 208);
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  -webkit-box-shadow: inset 0 -100px 0 rgba(33, 133, 208, 0.15);
          box-shadow: inset 0 -100px 0 rgba(33, 133, 208, 0.15);
}

table.data-grid td.cell.read-only,
table.data-grid th.cell.read-only,
table.data-grid td.cell.read-only.fixed-column,
table.data-grid th.cell.read-only.fixed-column {
  background: whitesmoke;
  color: #999;
  text-align: center;
}

table.data-grid td.cell > div.text,
table.data-grid th.cell > div.text {
  padding: 2px 5px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}


table.data-grid td.cell > input {
  outline: none !important;
  border: 2px solid rgb(33, 133, 208);
  text-align:right;
  width: calc(100% - 6px);
  height: 11px;
  background: none;
}


table.data-grid td.cell,
table.data-grid th.cell {
  vertical-align: bottom;
}

table.data-grid td.cell,
table.data-grid.wrap td.cell,
table.data-grid.wrap td.cell.wrap,
table.data-grid td.cell.wrap,
table.data-grid.nowrap td.cell.wrap,
table.data-grid.clip td.cell.wrap,

table.data-grid th.cell,
table.data-grid.wrap th.cell,
table.data-grid.wrap th.cell.wrap,
table.data-grid th.cell.wrap,
table.data-grid.nowrap th.cell.wrap,
table.data-grid.clip th.cell.wrap {
  white-space: normal;
}

table.data-grid.nowrap td.cell,
table.data-grid.nowrap td.cell.nowrap,
table.data-grid td.cell.nowrap,
table.data-grid.wrap td.cell.nowrap,
table.data-grid.clip td.cell.nowrap,

table.data-grid.nowrap th.cell,
table.data-grid.nowrap th.cell.nowrap,
table.data-grid th.cell.nowrap,
table.data-grid.wrap th.cell.nowrap,
table.data-grid.clip th.cell.nowrap {
  white-space: nowrap;
  overflow-x: visible;
}

table.data-grid.clip td.cell,
table.data-grid.clip td.cell.clip,
table.data-grid td.cell.clip,
table.data-grid.wrap td.cell.clip,
table.data-grid.nowrap td.cell.clip,

table.data-grid.clip th.cell,
table.data-grid.clip th.cell.clip,
table.data-grid th.cell.clip,
table.data-grid.wrap th.cell.clip,
table.data-grid.nowrap th.cell.clip {
  white-space: nowrap;
  overflow-x: hidden;
}

/* Fixed data sheet */
.data-grid-wrapper {
  position: relative;
  overflow: auto;
  border: 1px solid #ccc;
  border-collapse: collapse;
}

.data-grid-wrapper table.data-grid.dtg-virtual-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}

.data-grid-wrapper table.data-grid.dtg-main {
  position: absolute;
  z-index: 1;
}

.data-grid-wrapper table.dtg-virtual-header.scrolling thead tr:last-child {
  -webkit-box-shadow: 0px 1px 6px 1px #eaeaea;
          box-shadow: 0px 1px 6px 1px #eaeaea;
}

.data-grid-wrapper table.data-grid {
  border-collapse: separate;
  border-spacing: 0;
}

.data-grid-wrapper table.data-grid td.cell,
.data-grid-wrapper table.data-grid th.cell {
  border-top: none;
  border-left: none;
}

.data-grid-wrapper table.data-grid td.cell > input {
  height: inherit;
  width: 100%;
  padding: 0 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.data-grid-wrapper table.data-grid td.cell.selected,
.data-grid-wrapper table.data-grid th.cell.selected {
  border: 1px double rgb(33, 133, 208);
}

.data-grid-wrapper table.data-grid thead tr th.fixed-column,
.data-grid-wrapper table.data-grid tbody tr td.fixed-column {
  position: relative;
  z-index: 3;
  background: #fff;
}

.data-grid-wrapper table.data-grid thead tr th.fixed-column.last.scrolling,
.data-grid-wrapper table.data-grid tbody tr td.fixed-column.last.scrolling {
  border-right-width: 2px;
}

.data-grid-wrapper table.data-grid .fixed-column.last.scrolling:after {
  -webkit-box-shadow: 18px 0 11px -15px #eaeaea inset;
          box-shadow: 18px 0 11px -15px #eaeaea inset;
  content: " ";
  height: 100%;
  position: absolute;
  top: 0;
  right: -10px;
  width: 10px;
}


